.card {
  margin: 1em 0;
}

.card img {
  float: right;
}

.card-content:last-child {
  padding-bottom: 16px;
}

.horizontal-marginned {
  padding-top: 0px;
  padding-left: 16px;
  padding-right: 16px;
  padding-bottom: 0px;
}
.horizontal-marginned:last-child {
  padding-bottom: 0px;
}
.vertcial-marginned {
  padding-top: 16px;
  padding-left: 0px;
  padding-right: 0px;
  padding-bottom: 16px;
}
.vertcial-marginned:last-child {
  padding-bottom: 16px;
}

.chip {
  margin: 0.5ch;
}

/* 'pre-wrap' allows js break lines (\n) */
.summary-list > div {
  margin: 1ch 0;
  white-space: pre-wrap;
}

.grey {
  color: grey;
}

/* copy of Typography component */
.title {
  font-family: "Roboto","Helvetica","Arial",sans-serif;
  font-weight: 500;
  font-size: 1.25rem;
  line-height: 1.6;
  letter-spacing: 0.0075em;
  margin-right: 0.5em;
}

.white-background {
  filter: contrast(1.1);
}
